// Color system

$white:    #fff !default;
$gray-100: #f8f9fa !default;
$gray-200: #e9ecef !default;
$gray-300: #dee2e6 !default;
$gray-400: #ced4da !default;
$gray-500: #adb5bd !default;
$gray-600: #6c757d !default;
$gray-700: #495057 !default;
$gray-800: #343a40 !default;
$gray-900: #212529 !default;
$black:    #000 !default;

$dark: $gray-800 !default;

$aqua: #6bbaa7 !default;
$sunshine: #fba100 !default;
$lavender: #6c648b !default;
$dusty-rose: #b6a19e !default;

$colors: () !default;
$colors: map-merge((
        "white": $white,
        "dark": $dark,
        "aqua": $aqua,
        "sunshine": $sunshine,
        "lavender": $lavender,
        "dusty-rose": $dusty-rose
), $colors);

$primary: $lavender !default;
$secondary: $dusty-rose !default;
$tertiary: $aqua !default;
$quaternary: $sunshine !default;

$background-color: rgba($gray-800, 1) ;
